import React,{Component} from 'react';
import Main from './main';
import {Content} from 'react-mdl';
import Bottom from './bottom';
import './App.css';

class App extends Component{
  render(){
    return(
      <div>
        <Content>
          <Main />
        </Content>
        <Bottom />
      </div>
    );
  }
}
export default App;



